
import Search from 'vux/src/components/search/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

export default {
components: {
Search,
Scroller,
Divider,
LoadMore,
Group,
CellBox
},
data() {
return {

topImg: "http://www.hongkonganran.com/pic/gif/1440820472.jpg",
results: [],
keyWord: "",
onFetching: false,
bottomCount: 20
};
},
methods: {
resultClick(item) {
window.alert("you click the result item: " + JSON.stringify(item));
},
getResult(val) {
console.log("on-change", val);
this.results = val ? getResult(this.value) : [];
},
onSubmit() {
this.$refs.search.setBlur();
this.$vux.toast.show({
type: "text",
position: "top",
text: "on submit"
});
},
onFocus() {
console.log("on focus");
},
onCancel() {
console.log("on cancel");
},
onScrollBottom() {
if (this.onFetching) {

} else {
this.onFetching = true;
setTimeout(() => {
this.bottomCount += 10;
this.$nextTick(() => {
this.$refs.scrollerBottom.reset();
});
this.onFetching = false;
}, 2000);
}
}
}
};

function getResult(val) {
let rs = [];
for (let i = 0; i < 20; i++) {
rs.push({
title: `${val} result: ${i + 1} `,
other: i
});
}
return rs;
}



